nav {
    display: flex;
    padding: 2% 6%;
    justify-content: space-between;
    align-items: center;
    nav-up: current;
}


.nav-links {
    flex: 1;
    text-align: right;

}

.nav-links ul li {
    list-style: none;
    display: inline-block;
    padding: 8px 12px;
    position: relative;

}

.nav-links ul li a {
    color: rgb(15, 10, 10);
    text-decoration: none;
    font-size: 13px;

}

.nav-links ul li::after {
    content: '';
    width: 0%;
    height: 2px;
    display: block;
    margin: auto;
    background: red;
    transition: 0.5s;

}

.nav-links ul li:hover::after {
    content: '';
    width: 100%;
    height: 2px;
    display: block;
    margin: auto;
    background: red;
    transition: 0.5s;
}

.content {
    width: 1200px;
